import { useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { useRecoilState, useSetRecoilState } from 'recoil';
import styled from 'styled-components';
import { categoryState, customCategoriesState } from '../atoms';

const CATEGORIES_KEY = 'additional_categories';

interface IForm {
    customCategory: string;
}

function CreateCategory() {
    const {
        register,
        handleSubmit,
        setValue,
        formState: { errors },
    } = useForm<IForm>();
    const [customCategories, setCustomCategories] = useRecoilState(customCategoriesState);

    const handleValid = ({ customCategory }: IForm) => {
        setValue('customCategory', '');
        setCustomCategories((oldCategories) => {
            return [...oldCategories, customCategory];
        });
    };

    useEffect(() => {
        localStorage.setItem(CATEGORIES_KEY, JSON.stringify(customCategories));
    }, [customCategories]);

    return (
        <form onSubmit={handleSubmit(handleValid)}>
            <input
                {...register('customCategory', {
                    required: 'Ïπ¥ÌÖåÍ≥†Î¶¨Î•º ÏûÖÎ†• ÌõÑ ÏÉùÏÑ±ÏùÑ ÌÅ¥Î¶≠ÌïòÏÑ∏Ïöî.',
                })}
                placeholder="Ïó¨Í∏∞Ïóê ÏÉà Ïπ¥ÌÖåÍ≥†Î¶¨Î•º ÏûÖÎ†•ÌïòÏÑ∏Ïöî"
                type="text"
            />
            <button>ÏÉùÏÑ±</button>
            <span>{errors.customCategory?.message}</span>
        </form>
    );
}

export default CreateCategory;
