function ToDoList() {
    const toDos = useRecoilValue(toDoSelector);
    const [category, setCategory] = useRecoilState(categoryState);
    const onInput = (event: React.FormEvent<HTMLSelectElement>) => {
        setCategory(event.currentTarget.value as any);
    };
    const [customCategories, setCustomCategories] = useRecoilState(customCategoriesState);
    console.log(category);
    console.log(customCategories);

    useEffect(() => {
        localStorage.setItem(CATEGORIES_KEY, JSON.stringify(customCategories));
    }, [customCategories]);

    return (
        <div>
            <h1>To Dos</h1>
            <hr />
            <select value={category} onInput={onInput}>
                <option value={Categories.TO_DO}>To Do</option>
                <option value={Categories.DOING}>Doing</option>
                <option value={Categories.DONE}>Done</option>
                {customCategories.map((customCategory, idx) => (
                    <option value={customCategory} key={idx}>
                        {customCategory}
                    </option>
                ))}
            </select>
            <CreateCategory />
            <CreateToDo />
            {toDos?.map((toDo) => (
                <ToDo key={toDo.id} {...toDo} />
            ))}
        </div>
    );
}

export default ToDoList;
