import React from 'react';
import { useRecoilState, useSetRecoilState } from 'recoil';
import { Categories, IToDo, toDoState, customCategoriesState } from '../atoms';

function ToDo({ text, category, id }: IToDo) {
    const setToDos = useSetRecoilState(toDoState);
    const [customCategories, setCustomCategories] = useRecoilState(customCategoriesState);
    const onClick = (event: React.MouseEvent<HTMLButtonElement>) => {
        const {
            currentTarget: { name },
        } = event;
        setToDos((oldToDos) => {
            const targetIndex = oldToDos.findIndex((toDo) => toDo.id === id);
            const newToDo = { text, id, category: name as any };
            return [...oldToDos.slice(0, targetIndex), newToDo, ...oldToDos.slice(targetIndex + 1)];
        });
    };
    console.log(category);
    return (
        <li>
            <span>{text}</span>
            {category !== Categories.DOING && (
                <button name={Categories.DOING} onClick={onClick}>
                    Doing
                </button>
            )}
            {category !== Categories.TO_DO && (
                <button name={Categories.TO_DO} onClick={onClick}>
                    To Do
                </button>
            )}
            {category !== Categories.DONE && (
                <button name={Categories.DONE} onClick={onClick}>
                    Done
                </button>
            )}
            {customCategories.map((value, i) => {
                if (category !== customCategories[i]) {
                    return (
                        <button key={i} onClick={onClick} name={customCategories[i]}>
                            {customCategories[i]}
                        </button>
                    );
                }
            })}
        </li>
    );
}

export default ToDo;
